.content-contat {
    background-color: #0f0f0f;
    margin: 0 auto;     
}
.contat {
    margin: 0 auto;
    width: 80%;
    padding-top: 40px;
    padding-bottom: 40px;
}
.h2contat {
    color: white;
    font-size: 40px;
    font-style: oblique;
    border-bottom: 3px solid white;
}
.contat ul li {
    font-size: 20px;
    color: white;
    font-family: 'Fauna One' serif;
    list-style-type: none;
    border-bottom: 1px solid white;
}
.topcscontat {
    font-family: 'Fauna One' serif;
    font-style: oblique;
    font-size: 20px;
    color: white;
    margin-bottom: 40px;
    border-bottom: 1px solid #54c8ff;
}
.topcs {
    font-family: 'Fauna One' serif;
    font-style: oblique;
    font-size: 15px;
    color: white;
    margin-bottom: 30px;
}
.btcontat {
    padding-right: 15px; 
    padding-top: none !important;
    padding-bottom: none !important;
}
.popup {
    font-family: 'Fauna One' serif !important;
    font-style: oblique !important;
    font-size: 15px !important;
    height: 120px !important;
    padding: none !important;
}
