.menu {
    position: fixed;
    z-index: 20;
    border-bottom: 2px solid black ;
    background-color: white;
    backdrop-filter: blur(10px);
    text-align: center;
    filter: blur 20px;
    width: 100%;
}
.menu ul li {
    text-decoration: none !important;
    list-style-type: none;
    display: inline-block;
    padding-left: 40px;
    padding-right: 40px; 
    margin-left: 40px;
    margin-right: 40px;
    font-family: 'Fauna One' serif;
    font-size: 22px;
    color: #1b1a1c;

}
.menu ul li:hover {
    color: lightgray !important;
}
.menu ul li:active {
    color: #474350;
}
