.overlayjs {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: rgba(251, 189, 8, 0.4); 
    filter: blur(1px);
    -webkit-filter: blur(1px);
} 
.card:hover .overlayjs {
    opacity: 1;
}
