}
.cv {
    margin: 0 auto;
    width: 80%;
    padding-top: 40px;
    padding-bottom: 40px;
}
.h2cv {
    color: white;
    font-size: 40px;
    font-style: oblique;
    border-bottom: 3px solid white;
}
.cv ul li {
    font-size: 20px;
    color: white;
    font-family: 'Fauna One' serif;
    list-style-type: none;
    border-bottom: 1px solid white;
}
.topcscv {
    font-family: 'Fauna One' serif;
    font-style: oblique;
    font-size: 20px;
    color: white;
    margin-bottom: 30px;
    border-bottom: 1px solid #ff8edf;
}
.topcs {
    font-family: 'Fauna One' serif;
    font-style: oblique;
    font-size: 15px;
    color: white;
    margin-bottom: 30px;
}
