.content-atvextra {
    background-color: #474350;
    margin: 0 auto;   
}
.atvextra {
    margin: 0 auto;
    width: 80%;
    padding-top: 40px;
    padding-bottom: 40px;
}
.h2atv {
    color: white;
    font-size: 40px;
    font-style: oblique;
    border-bottom: 3px solid white;
}
.atvextra ul li {
    font-size: 20px;
    color: white;
    font-family: 'Fauna One' serif;
    list-style-type: none;
    border-bottom: 1px solid white;
}
.topcs {
    font-family: 'Fauna One' serif;
    font-style: oblique;
    font-size: 20px;
    color: white;
}
.caixa {
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(255,255,255, 1); /* Black w/opacity/see-through */
    color: black;
    border: 1px solid black;
    box-shadow: 2px 2px #474350;
    position: relative;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    width: 300px;
    height: 300px;
    padding: 20px;
    text-align: center;
    font-family: 'Fauna One' serif;
    font-size: 25px;
} 
.caixa:hover {
    background-color: white;
    background-color: rgba(255,255,255, 0.4); /* Black w/opacity/see-through */
} 
.caixab {
    position: relative;
    top: -250px;
    left: 50px;
    color: black;
    opacity: 0;
}
.text {
    font-size: 30px;
    font-family: 'Fauna One' serif;
    color: white;
    font-style: oblique;
    border-bottom: 1px solid white;
    width: 245px;
}
.text2 {
    font-size: 20px;
    font-family: 'Fauna One' serif;
    color: white;
    font-style: oblique;
}
.header-1 {
    font-family: 'Fauna One' serif;
    color: white !important;
    font-style: oblique;
    font-size: 25px !important;
    border-bottom: 1px solid yellow;
}
.cont- {
    font-family: 'Fauna One' serif;
    color: white !important;
    font-style: oblique;
    font-size: 15px;
}
.header-r {
    font-family: 'Fauna One' serif;
    color: white !important;
    font-style: oblique;
    font-size: 25px !important;
    border-bottom: 1px solid #54c8ff;
    text-align: right;
    margin-right: 20px !important;
}
.header-l {
    font-family: 'Fauna One' serif;
    color: white !important;
    font-style: oblique;
    font-size: 25px !important;
    border-bottom: 1px solid #2ecc40;
    margin-right: 20px !important;
}
.img {
    display: block;
    margin: 0 auto;
    color: #252934;
    overflow: hidden;
    text-align: center;
    border-radius: 50%;
    border: 1px solid #474350;
    box-shadow: 0 20px 10px -10px #25293450;
    transform: scale(1.08);
    transition: 200ms;
}
  
 .img:hover {
    box-shadow: 0 50px 15px -30px #25293450;
    transition: 200ms;
}  
.img:hover > img {
    transform: scale(1.2);
    transition: 200ms;
}
.link {
    color: white !important;
    font-family: 'Fauna One' serif;
    margin: 2px;
}
.bt {
    margin-top: 2px !important;
    float: right; 
}
.bt:hover {
    color: white !important;
}
.btl {
    margin-top: 2px !important;
    float: left; 
}

  

  